.info-list{
    display: flex;
    flex-direction: column;
    border-top: 20px solid red;
}
.bug-list{
    display: flex;
    flex-direction: column;
    width:100%;
    justify-content: center;
    align-items: center;
    background-color: white; 
}

.bug-card{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    padding-bottom: 20px;
    border-bottom: 20px solid red;
}

.info-list.bug-list.bug-card ul{
    width: 100%;
    list-style-type: disc;
    display: flex;
    flex-direction: column;
}

.bug-card ul li{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
   padding: 10px 5px;
   margin: 0 5px;
   border-top: 1px solid red;
}

.bug-card h3, .bug-card h2{
    font-weight:600;
}

.info-list ul .hidden{
    display: none;

}
.bug-header{
    cursor: pointer;
    width: 100%;
    height: 2rem;
   text-align: center;
   background-color: lightgray;
   display: block;
   vertical-align: middle;
}
.disclaimer{
    padding: 10px;
    font-size: .7rem;
    text-align: center;
    border-bottom: 10px solid red;
}


@media only screen and (min-width: 768px) {

    .bug-card ul li{
        list-style-type: square;
        border: none;
     }

}