.header{
    font-size: .8rem;
   
}
.banner{
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {

/* Containers */
.header{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.banner{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}


/* banner items */
.logo{
    width: 10%;
   
}
.header h1{
    font-weight: 700;
}
.contact{

}

}
