nav{
display: block;
width: 100%;
height: 3rem;
background-color: white;
position: fixed;
top: 0;
margin: 0;
}

#bars{

    width: 40px;
    height: 40px;
    margin: 10px;
    display: flex;  
    align-items: center;   
    justify-content: center;   
    font-size: 2rem;

}
.navbar{
    display: block;
}
.barsIsClicked{


    top:0;
    width:95%;

}
nav ul{

    list-style: none;
    text-align: center;
    box-sizing: border-box;
    padding-left: 0px;

}
.hidden{
    display: none;

}

nav ul li{

    display: block;
    background-color: white;
    border: 1px solid black;
}

nav ul li:hover{


    background-color: red;
    color:white;
}

nav ul li a:hover{
    color:white;
}

.hide-line{
    display: none;
}


/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {

    nav{
        display: block;
        margin: 0;
        width:100%;
    }
    nav ul{
        display: flex;
        flex-direction: row; 
        justify-content: flex-end;
        background-color: transparent;
    }
    nav ul li{
        display: block;
        padding:5px;
        border: none;
        background-color: transparent;
        font-weight: 200;
    }
    .hide-line,.hidden{
        display: block;
    }
    #bars{
        display: none;
    }

}

