

.landing{
    display: flex;
    flex-direction: column;
    font-size: 1rem;
    font-weight: 500;
    
    }

.landing main{
        display: grid;
        background-color: rgb(238, 4, 4);
        grid-gap: .5rem;
        width: 100%;
       grid-template-columns: 1fr;
       grid-auto-rows: minmax(100px,auto);
        grid-template-areas: 
        "header"
        "background"
        "content" ;
    }


/* landing items */
.landing-background{
    grid-area:background;
}

.landing-background img{
    width: 100%;
    height: auto;
}


.landing main .content{
    text-align: center;
    justify-self: center;
    font-size: 1rem;
    align-self: center;
    grid-area:content;
    border-radius: 2px;
    padding: 5px 10px;
    background-color: white;
    margin-bottom: 20px;

}
.landing main h2{
    text-align: center;
    justify-self: center;
    align-self: center;
    font-size: 1rem;
    width: 100%;
    font-weight: 600;
    grid-area: header;
    padding:10px 100px;
    border-radius: 2px;
    background-color: white;
}

.wiifm-label{
    display: flex;
    text-align: center;
    align-items: center;
    height: auto;
    justify-self: center;
    align-self: center;
    border-radius: 2px;
    padding: 5px 10px;
    background-color: white;

}

.estimate-label{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    justify-self: center;
    align-self: center;
    z-index: 10;
    border-radius: 2px;
    padding: 5px 10px;
    background-color: white;
}




/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {

/* Container */
.landing{
display: flex;
flex-direction: column;
font-size: 1.2rem;
font-weight: 600;

}
.landing main{
    display: grid;
    background-color: rgb(238, 4, 4);
    grid-gap: 1rem;
    width: 100%;
   grid-template-columns: repeat(4,1fr);
   grid-auto-rows: minmax(100px,auto);
    grid-template-areas: 
    "header header header header"
    "background background background background"
    " content content content content"
    ;
}

/* landing items */
.landing-background{
    grid-area:background;
}

.landing-background img{
    width: 100%;
    height: 100%;;
}


.landing main .content{
    justify-self: center;
    padding:20px;
    font-size: 1.5rem;
    align-self: center;
    grid-area:content;
    grid-column-start: 2;
    grid-column-end: 4;
    border-radius: 20px;
    background-color: white;
    margin-bottom: 20px;

}
.landing main h2{
    text-align: center;
    align-self: center;
    font-size: 1.5rem;
    font-weight: 600;
    grid-area: header;
    padding:5px 20px;
    border-radius: 10px;
    background-color: white;
}

.wiifm-label{
    display: flex;
    align-items: center;
    width: 200px;
    height: auto;
    grid-column-start: 4;
    grid-row-start: 2;
    justify-self: center;
    align-self: center;


}

.estimate-label{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 200px;
    grid-column-start: 1;
    grid-row-start: 2;
    justify-self: center;
    align-self: center;
    z-index: 10;

}
}